import { people_list,user_add_follow,question_list } from '../../api/api.js'
export default {
	data() {
		return {
			access_token:'',
			member_id:'',
			searchContent: '',
			tabCurrentIndex: 1,
			questionList: [],
			peopleList: [],
			navList: [
				{
					state: 0,
					text: '新闻'
				},
				{
					state: 1,
					text: '热门问答'
				},
				{
					state: 2,
					text: '热门资料'
				},
				{
					state: 3,
					text: '达人'
				}
			],
			labels: [
				{
					label: "自动化",
				},
				{
					label: "仪器控",
				}
			]
		}
	},
	onLoad() {
		let that = this
		uni.getStorage({
		    key: 'access_token',
		    success: function (res) {
		        that.access_token = res.data
		    }
		});
		uni.getStorage({
		    key: 'member_id',
		    success: function (res) {
		        that.member_id = res.data
		    }
		});
		console.log(that.member_id)
		console.log(that.access_token)
		that.getHotQuestion()
	},
	methods: {
		// tab切换
		tabClick(index){
			let that = this
			this.tabCurrentIndex = index
			if(this.tabCurrentIndex == 3){
				that.getPeopleList()
			}else if(this.tabCurrentIndex == 1){
				that.getHotQuestion()
			}
		},
		//点击跳转问题
		toQuestionDetail(id){
			uni.navigateTo({
				url: '../questionDetail/questionDetail?id=' + id,
			})
		},
		// 获取热门问答列表
		getHotQuestion(){
			let that = this
			question_list({
				member_id: that.member_id,
				access_token: that.access_token,
				type: 3, //1-推荐，2-最新，3-热门，4-冷门
				seartag: '',//筛选的标签（多个标签 id 用,号隔开）
				reward : 2,//1-升序，2-降序
				page: 1,
				limit: 10
			}).then(res => {
				console.log(res)
				if(res.data.code == 1){
					that.questionList = res.data.data.list
				}
			})
		},
		// 获取达人列表
		getPeopleList(){
			let that = this
			people_list({
				member_id: that.member_id,
				access_token: that.access_token,
				page: 1,
				limit : 10
			}).then(res => {
				console.log(res)
				if(res.data.code == 1){
					that.peopleList = res.data.data.list
				}
			})
		},
		// 关注达人
		focusPeople(id,type){
			let that = this
			user_add_follow({
				member_id: that.member_id,
				access_token: that.access_token,
				to_member_id: id,
			}).then(res => {
				console.log(res)
				let msg
				if(res.data.code == 1){
					if(type == 1){
						 msg = "关注达人成功!"
					}else if(type == 2){
						msg = "取消成功!"
					}
					uni.showToast({
						title: msg,
						duration: 2000,
						icon: 'none'
					});
					that.getPeopleList()
				}else{
					uni.showToast({
						title: res.data.msg,
						duration: 2000,
						icon: 'none'
					});
				}
			})
		},
	}
}
